.clearfix:after,
.header .header-inner:after,
.header .nav-wrap .main-nav:after,
.info-list .form-group:after,
.step-flow-list:after {
  content: '';
  display: table;
  clear: both;
}
a:hover,
a:focus {
  text-decoration: none;
}
.no-list-style,
.ui-checkbox-list,
.header .nav-wrap .main-nav,
.sidebar .side-nav,
.sidebar .side-nav .side-subnav,
.step-flow-list,
.sel-list-wrap .sel-list,
.sel-list-wrap .sel-inner-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.space-line {
  margin: 0 10px;
}
.devider-line {
  border-bottom: 1px solid #f0f3f6;
}
.long-size.btn {
  min-width: 200px;
}
.primary-bd {
  border: 1px solid #99daff;
  border-top: none;
}
.ui-checkbox-list {
  margin-bottom: -5px;
}
.ui-checkbox-list .checkbox-item {
  position: relative;
  display: inline-block;
  margin: 0 5px 5px 0;
  padding: 5px 25px 5px 10px;
  background: #ebedf0;
  cursor: pointer;
}
.ui-checkbox-list .checkbox-item.active,
.ui-checkbox-list .checkbox-item:hover {
  color: #333;
}
.ui-checkbox-list .checkbox-item.active:before,
.ui-checkbox-list .checkbox-item:hover:before {
  content: '';
  position: absolute;
  right: 0;
  bottom: 0;
  display: block;
  height: 0;
  width: 0;
  border-style: solid solid none none  ;
  border-color: transparent #5cb85c;
  border-width: 20px 20px 0 0 ;
}
.ui-checkbox-list .checkbox-item.active:after,
.ui-checkbox-list .checkbox-item:hover:after {
  content: '\e013';
  font-size: 12px;
  position: absolute;
  right: 0;
  bottom: 1px;
  display: inline-block;
  font-family: 'Glyphicons Halflings';
  font-style: normal;
  font-weight: 400;
  line-height: 1;
  color: #fff;
}
.mg-right-sm {
  margin-right: 5px !important;
}
.mg-left-base {
  margin-left: 10px !important;
}
.mg-top-mid {
  margin-top: 20px !important;
}
.mg-top-base {
  margin-top: 10px !important;
}
.mg-vertical-base {
  margin-top: 10px !important;
  margin-bottom: 10px !important;
}
/*头部*/
.header {
  height: 72px;
  background: #00a2ff;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
}
.header .header-inner {
  color: #fff;
}
.header .logo-wrap {
  float: left;
  line-height: 37px;
}
.header .logo-wrap .logo {
  height: 75px;
  width: 296px;
  vertical-align: middle;
}
.header .nav-wrap {
  float: right;
}
.header .nav-wrap .main-nav .nav-item {
  float: left;
  border-left: 1px solid #0091e4;
}
.header .nav-wrap .main-nav .nav-item .user-info-wrap {
  padding: 10px 40px;
  text-align: left;
}
.header .nav-wrap .main-nav .nav-item .user-info-wrap .btn-outline-primary:hover,
.header .nav-wrap .main-nav .nav-item .user-info-wrap .btn-outline-primary:focus {
  background: #006199;
}
.header .nav-wrap .main-nav .nav-item .user-info-wrap .user-info {
  margin: 5px 0 0;
}
.header .nav-wrap .main-nav .nav-item.active .nav-link {
  background: #5fb2ff;
}
.header .nav-wrap .main-nav .nav-item .nav-link {
  display: block;
  color: #fff;
  font-size: 14px;
  padding-top: 10px;
  padding-left: 8px;
  padding-right: 8px;
  height: 72px;
  text-align: center;
}
.header .nav-wrap .main-nav .nav-item .nav-link:hover {
  background: #5fb2ff;
}
.header .nav-wrap .main-nav .nav-item .nav-link .name {
  margin-top: 5px;
  line-height: 1;
}
.header .nav-wrap .main-nav .nav-item .nav-link.quit-link {
  padding-right: 0;
  background: transparent;
  padding-top: 26px;
}
.gs-container {
  min-width: 1000px;
  margin: 0 auto 0;
}
.sidebar {
  background: #00a2ff;
  position: fixed;
  top: 82px;
  bottom: 0;
  width: 190px;
}
.sidebar .side-nav-wrap {
  padding: 20px 0;
  overflow-y: auto;
}
.sidebar .side-nav .nav-item.active > .nav-link {
  background: #5fb2ff;
}
.sidebar .side-nav .nav-item.active .nav-flag {
  background: url('../images/side_bar_flag_on.png') center center;
}
.sidebar .side-nav .nav-flag {
  display: inline-block;
  margin-right: 5px;
  height: 6px;
  width: 6px;
  background: url('../images/side_bar_flag.png') center center;
  vertical-align: middle;
}
.sidebar .side-nav .nav-link {
  display: block;
  position: relative;
  padding: 0 30px;
  color: #fff;
  line-height: 40px;
  font-size: 14px;
}
.sidebar .side-nav .nav-link:hover {
  background: #5fb2ff;
}
.sidebar .side-nav .side-subnav .nav-link {
  padding-left: 60px;
}
.main-wrap {
  padding-left: 200px;
  padding-top: 82px;
}
.gs-container,
.header-inner {
  margin-left: auto;
  margin-right: auto;
  min-width: 1020px;
  max-width: 1420px;
  padding-left: 10px;
  padding-right: 10px;
}
@media screen and (min-width: 1220px) {
  .gs-container,
  .header-inner {
    width: 1200px;
  }
  .gs-container .nav-wrap .main-nav .nav-item .nav-link,
  .header-inner .nav-wrap .main-nav .nav-item .nav-link,
  .gs-container .nav-wrap .main-nav .nav-item .user-info-wrap,
  .header-inner .nav-wrap .main-nav .nav-item .user-info-wrap {
    padding: 10px 20px 0;
    height: 72px;
  }
}
.app-tabs-nav > .ui-tabs-nav {
  background: #ebecf2;
  padding: 5px 20px 0;
}
.app-tabs-nav > .ui-tabs-nav > li {
  margin-right: 2px;
}
.app-tabs-nav > .ui-tabs-nav > li.tabs-arrow > a {
  min-width: 0;
  background: #ebecf2;
  padding-left: 5px;
}
.app-tabs-nav > .ui-tabs-nav > li.tabs-arrow > a:hover {
  padding-left: 5px;
}
.app-tabs-nav > .ui-tabs-nav > li.tabs-arrow.next {
  float: right;
}
.app-tabs-nav > .ui-tabs-nav > li.ui-tabs-active > a {
  background: #fff;
  border: none;
  padding: 6px 5px 6px 15px;
}
.app-tabs-nav > .ui-tabs-nav > li.ui-tabs-active > a:hover {
  background: #fff;
  padding: 6px 5px 6px 15px;
  border: none;
}
.app-tabs-nav > .ui-tabs-nav > li.ui-tabs-active > a .close {
  visibility: visible;
}
.app-tabs-nav > .ui-tabs-nav > li > a {
  color: #838387;
  border: none;
  background: #f0f3f6;
  padding: 6px 5px 6px 15px;
  min-width: 90px;
  margin: 0;
  white-space: nowrap;
}
.app-tabs-nav > .ui-tabs-nav > li > a:hover,
.app-tabs-nav > .ui-tabs-nav > li > a:focus,
.app-tabs-nav > .ui-tabs-nav > li > a:active {
  border: none;
  padding: 6px 5px 6px 15px;
  background: #fff;
}
.app-tabs-nav > .ui-tabs-nav > li > a:hover .close,
.app-tabs-nav > .ui-tabs-nav > li > a:focus .close,
.app-tabs-nav > .ui-tabs-nav > li > a:active .close {
  visibility: visible;
  animation: all 2s;
}
.app-tabs-nav > .ui-tabs-nav > li .close {
  visibility: hidden;
  border-radius: 100%;
  background: #cfcfcf;
  width: 18px;
  height: 18px;
  vertical-align: top;
  line-height: 1;
  padding-bottom: 2px;
  padding-bottom: 0 \9;
  text-align: center;
  font-size: 16px;
  margin-left: 5px;
}
.search-bar .form-control {
