}
.sideup-bar {
  border-top: 1px solid #f0f3f6;
  text-align: center;
}
.sideup-bar .sideup-bar-btn {
  display: inline-block;
  height: 24px;
  width: 200px;
  text-align: center;
  line-height: 24px;
