}
.accounting-relation-title {
  display: inline-block;
  font-size: 15px;
  color: #00a2ff;
  background: #fff;
  font-weight: 600;
  padding: 4px 20px;
}
.step-flow-list .list-item {
  float: left;
  width: 33.333%;
  padding: 0 3px;
  position: relative;
}
.step-flow-list .list-item.active .inner {
  background: #00a2ff;
  color: #fff;
}
.step-flow-list .list-item.active + .list-item:after {
  border-left-color: #00a2ff;
}
.step-flow-list .list-item + .list-item:after,
.step-flow-list .list-item + .list-item:before {
  content: '';
  position: absolute;
  display: block;
  left: -3px;
  top: 0;
  height: 0;
  width: 0;
  border-style: solid none solid solid;
  border-color: transparent #fff;
  border-width: 15px;
  z-index: 3;
}
.step-flow-list .list-item + .list-item:before {
  left: 3px;
  border-color: transparent #f4f4f4;
  z-index: 2;
}
.step-flow-list .list-item .inner {
  padding: 6px 20px;
  position: relative;
  text-align: center;
  color: #9b9b9b;
  white-space: nowrap;
  background: #fff;
  z-index: 1;
}
.sel-list-wrap .sel-list .list-item {
  padding: 20px 0;
}
.sel-list-wrap .sel-list .list-item + .list-item {
  border-top: 1px solid #f0f3f6;
}
.sel-list-wrap .inner-flag {
  display: inline-block;
  margin-top: 15px;
}
.sel-list-wrap .inner-flag .flag-wrap {
  position: relative;
  padding: 8px 0;
}
.sel-list-wrap .inner-flag .flag-wrap .inner-flag-img {
  height: 44px;
  width: 44px;
  position: absolute;
  top: 0;
  left: 10px;
}
.sel-list-wrap .inner-flag .flag-wrap .flag-text-box {
  padding: 7px 20px 7px 69px;
  background: #00a2ff;
  color: #fff;
  white-space: nowrap;
}
.sel-list-wrap .sel-inner-list {
  padding: 4px;
}
.sel-list-wrap .sel-inner-list .inner-item {
  display: inline-block;
  position: relative;
  padding: 5px 20px;
}
.sel-list-wrap .sel-inner-list .inner-item + .inner-item:before {
  content: '|';
  position: absolute;
  left: 0;
  top: 50%;
  color: #ccc;
  margin-top: -8px;
}
.sel-list-wrap .sel-inner-list .inner-item.active .inner-link,
.sel-list-wrap .sel-inner-list .inner-item:hover .inner-link {
  box-shadow: 0 0  8px 2px #ebebeb;
}
.sel-list-wrap .sel-inner-list .inner-link {
  color: #757575;
  display: block;
  padding: 20px 20px;
}
.create-customer-wrap .customer-img-wrap {
