//公共
.clearfix{
    &:after{
        content:'';
        display: table;
        clear: both;
    }
}
a{
    &:hover,
    &:focus{
        text-decoration: none;
    }
}
.no-list-style{
    list-style: none;
    padding: 0;
    margin: 0;
}
.space-line{
    margin: 0 @padding-base-h;
}
.devider-line{
    border-bottom: 1px solid @border-color;
}
.long-size{
    &.btn{
        min-width: 200px;
    }
}
.primary-bd{
    border: 1px solid lighten(@brand-primary,30%);
    border-top: none;
}
.ui-checkbox-list{
    margin-bottom: -@padding-small-v;
    &:extend(.no-list-style);
    .checkbox-item{
        position: relative;
        display: inline-block;
        @height: @input-height-base;
        margin: 0 @padding-small-h @padding-small-v 0;
        padding: floor((@height - @line-height-computed) /2) @padding-base-h + 15 floor((@height - @line-height-computed) /2) @padding-base-h;
        background: #ebedf0;
        cursor: pointer;
        &.active,
        &:hover{
            color: @title;
            &:before{
                content:'';
                position: absolute;
                right: 0;
                bottom: 0;
                display: block;
                height: 0;
                width: 0;
                border-style: solid solid none none  ;
                border-color: transparent @brand-success ;
                border-width: 20px 20px 0 0 ;
            }
            &:after{
                content: '\e013';
                font-size: 12px;
                position: absolute;
                right: 0;
                bottom: 1px;
                bottom: 1px;
                display: inline-block;
                font-family: 'Glyphicons Halflings';
                font-style: normal;
                font-weight: 400;
                line-height: 1;
                color: #fff;
            }
        }
        
    }
}
//边距
.mg-right-sm{ margin-right: @padding-small-h !important;}
.mg-left-base{ margin-left: @padding-base-h !important;}
.mg-top-mid{margin-top: @padding-mid-v !important;}
.mg-top-base{margin-top: @padding-base-v !important;}
.mg-vertical-base{margin-top: @padding-base-v !important;margin-bottom: @padding-base-v !important;}
//结构
/*头部*/
.header{
    height: @header-height;
    background: @brand-primary;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    .header-inner{
        // margin: 0 @padding-mid-h;
        &:extend(.clearfix all);
        color: #fff;
    }
    .logo-wrap{
        float: left;
        @logo-height: 37px;
        line-height: @logo-height;
        .logo{
            height: 75px;
            width: 296px;
            vertical-align: middle;
        }
    } 
    .nav-wrap{
        float: right;
        .main-nav{
            &:extend(.no-list-style);
            &:extend(.clearfix all);
            .nav-item{
                float: left;
                border-left: 1px solid #0091e4;
                .user-info-wrap{
                    padding: @padding-base-v @padding-base-h*4;
                    text-align: left;
                    .btn-outline-primary{
                        &:hover,
                        &:focus{
                            background: darken(@brand-primary,20%);
                        }
                    }
                    .user-info{
                        margin: @padding-small-v 0 0;
                    }
                }
                &.active{
                    .nav-link{
                        background: #5fb2ff;
                    }
                }
                .nav-link{
                    display: block;
                    color: #fff;
                    font-size: @font-size-mid;
                    padding-top: @padding-base-v;
                    padding-left: @padding-small-h + 3;
                    padding-right: @padding-small-h+ 3;
                    height: @header-height; 
                    text-align: center;
                    
                    &:hover{
                        background: #5fb2ff;
                    }
                    .name{
                        margin-top: @padding-small-v;
                        line-height: 1;
                    }
                    &.quit-link{
                        padding-right: 0;
                        background: transparent;
                        padding-top: floor((@header-height - 20)/2);
                    }
                }
            }
        }
    }
}
.gs-container{
    min-width: @container-width;
    margin: 0 auto 0;
}
.sidebar{
    background: @brand-primary;
    position: fixed; 
    top: @header-height + @padding-base-v;
    bottom: 0;
    width: @sidebar-width;
    .side-nav-wrap{
        padding: @padding-mid-v 0;
        overflow-y: auto;
    }
    .side-nav{
        &:extend(.no-list-style);
        .nav-item{
            &.active{
                >.nav-link{
                    background: @side-nav-item-link-hover-bg;
                }
                .nav-flag{
                    background: url('../images/side_bar_flag_on.png') center center;
                }
            }
        }
        .nav-flag{
            display: inline-block;
            margin-right: @padding-small-h;
            height: 6px;
            width: 6px;
            background: url('../images/side_bar_flag.png') center center;
            vertical-align: middle;
        }
        .nav-link{
            display: block;
            position: relative;
            padding: 0 @padding-base-h*3;
            color: #fff;
            line-height: @side-nav-item-height;
            font-size: @font-size-mid;
            &:hover
           {
                background: @side-nav-item-link-hover-bg;
            }
            
        }
        .side-subnav{
            &:extend(.no-list-style);
            .nav-link{
                padding-left: @padding-base-h*6;
            }
        }
    }
}
.main-wrap{
    padding-left:@sidebar-width + @padding-base-h;
    padding-top: @header-height+@padding-base-v;
}
.gs-container,
.header-inner{
    margin-left: auto;
    margin-right: auto;
    min-width: 1020px;
    max-width: 1420px;
    padding-left: 10px;
    padding-right: 10px;
    @media screen and (min-width:1024px){
        .nav-wrap .main-nav .nav-item {
            .nav-link,
            .user-info-wrap{
                
            }
        }
    }
    @media screen and (min-width:1220px){
        width: 1200px;
        .nav-wrap .main-nav .nav-item {
            .nav-link,
            .user-info-wrap{
                padding: @padding-base-v @padding-small-h*4 0;
                height: @header-height;
            }
        }
    }
}
//标签样式
.app-tabs-nav{
    @a-height: 30px;
    >.ui-tabs-nav{
        background: #ebecf2;
        padding: @padding-small-v @padding-base-h*2 0;
        >li{
            margin-right: 2px;
            &.tabs-arrow{
                >a{
                    // padding-right: ;
                    min-width: 0;
                    background: #ebecf2;
                    padding-left: @padding-small-h;
                    &:hover{
                        padding-left: @padding-small-h;
                    }
                }
                &.next{
                    float: right;
                }
            }
            &.ui-tabs-active{
                >a{
                    background: #fff;
                    border: none;
                    padding: floor(( @a-height - @line-height-computed)/2) @padding-small-h floor(( @a-height - @line-height-computed)/2) @padding-small-h*3;
                    &:hover{
                        background: #fff;
                        padding: floor(( @a-height - @line-height-computed)/2) @padding-small-h floor(( @a-height - @line-height-computed)/2) @padding-small-h*3;
                        border: none;
                    }
                    .close{
                        visibility: visible;
                    }
                }
            }
            >a{
                color: #838387;
                border: none;
                background: #f0f3f6;
                padding: floor(( @a-height - @line-height-computed)/2) @padding-small-h floor(( @a-height - @line-height-computed)/2) @padding-small-h*3;
                min-width: 90px;
                margin: 0;
                white-space: nowrap;
                &:hover,
                &:focus,
                &:active{
                    border: none;
                    padding: floor(( @a-height - @line-height-computed)/2) @padding-small-h floor(( @a-height - @line-height-computed)/2) @padding-small-h*3;
                    background: #fff;
                    .close{
                        visibility: visible;
                        animation: all 2s;
                    }
                }
            }
            .close{
                    visibility: hidden;
                    border-radius: 100%;
                    background: #cfcfcf;
                    width: 18px;
                    height: 18px;
                    vertical-align: top;
                    line-height: 1;
                    padding-bottom: 2px;
                    padding-bottom: 0 \9;
                    text-align: center;
                    // line-height: 12px;
                    font-size: 16px;
                    margin-left: @padding-small-h;
            }
        }
        
    }
    
}

//search-bar
.search-bar{
    
    .form-control{
        width: 400px;
        border-color: @brand-primary;
    }
    .search-bar-group{
        margin-top: @padding-small-v*5; 
        .form-control{
            display: table-cell;
            width: 100%;
        }
        display: inline-table;
        .search-bar-btn{
            display: table-cell;
            width: 1%;
        }
    }
    
}
.info-list{
    .key{
        color: #4d4d4d;
        text-align: right;
    }
    .value{
        color: @text-tips-color;
    }
    .form-group{
        margin-bottom: @padding-base-v - 4;
        &:extend(.clearfix all);
    }
}
//col
.col-per-11,
.col-per-89{
    float: left;
    padding: @padding-small-h;
}
.col-2{
    width: 20%;
    float: left;
}
.col-per-11{
    width: 11%;
}
.col-per-89{
    width: 89%;
}
.pagination-wrap{ 
    text-align: right;
    .pagination{
        margin: @padding-base-v -9px 0 0;
    }
}
.row-btn{
    height: 20px;
    width: 20px;
    background: #b5b5b5;
    border-radius: 100%;
    text-align: center;
    color: #fff;
    line-height: 21px;
    padding-left: 1px;
    cursor: pointer;
    &.on{
        background: @brand-primary;
