    }
}
//客户中心页
.customer-center-wrap{
    .user-img{
        border-radius: 100%;
        height: 100px;
        width: 100px;
        margin-right: @padding-base-h ;
    }
    .view-wrap{
        margin-top: @padding-small-v*6;
        .btn{
            min-width: 150px;
        }
    }
    .card-list-wrap{
        text-align: center;
        margin: @padding-base-v 0;
    }
    
}
.sideup-bar{
    border-top: 1px solid @border-color;
    text-align: center;
    .sideup-bar-btn{
        display: inline-block;
        height: 24px;
        width: 200px;
        text-align: center;
        line-height: 24px;
        background: #f0f3f6;
    }
}

//改账户关系
.accounting-relation-title{
    @height: 32px;
    display: inline-block;
    font-size: @font-size-large;
    color: @brand-primary;
    background: #fff;
    font-weight: 600;
    padding: floor((@height - @font-size-large*@line-height-base)/2) @padding-mid-h;
}
.step-flow-list{
    @height: 30px;
    @list-item-padding:@padding-small-h - 2;
    &:extend(.clearfix all);
    &:extend(.no-list-style);
    .list-item{
        float: left;
        width: 33.333%;
        padding: 0 @list-item-padding;
        position: relative;
        &.active{
            .inner{
                background: @brand-primary;
                color: #fff;
            }
            &+.list-item{
                &:after{
                    border-left-color: @brand-primary;
                }
            }
        }
        &+.list-item{
            &:after,
            &:before{
                content: '';
                position: absolute;
                display: block;
                left: -@list-item-padding;
                top: 0;
                height: 0;
                width: 0;
                border-style: solid none solid solid;
                border-color: transparent #fff;
                border-width: 30px/2;
                z-index: 3;
            }
            &:before{
                left: @list-item-padding;
                border-color: transparent @body-bg;
                z-index: 2;
            }
        }
        .inner{
            padding: floor((@height - @line-height-computed) /2) @padding-small-h*4;
            position: relative;
            text-align: center;
            color: @text-tips-color;
            white-space: nowrap;
            background: #fff;
            z-index: 1;
            
            
            
        }
    }
}
.sel-list-wrap{
    .sel-list{
       &:extend(.no-list-style);
        .list-item{
            &+.list-item{
                border-top: 1px solid @border-color;
            }
            padding: @padding-mid-v 0;
        }
    }
    .inner-flag{
        display: inline-block;
        margin-top: @padding-base-v + @padding-small-v;
        .flag-wrap{
            position: relative;
            padding: @padding-base-v - 2 0;
            .inner-flag-img{
                height: 44px;
                width: 44px;
                position: absolute;
                top: 0;
                left: @padding-base-h;
            }
           .flag-text-box{
               padding: @padding-base-v - 3 @padding-mid-h @padding-base-v - 3 @padding-small-h*5+44;
               background: @brand-primary;
               color: #fff;
               white-space: nowrap;
           } 
        }
    }
    .sel-inner-list{
       &:extend(.no-list-style);
       padding: 4px;
        .inner-item{
            display: inline-block;
            position: relative;
            padding: 5px 20px;
            &+.inner-item{
                &:before{
                    content:'|';
                    position: absolute;
                    left: 0;
                    top: 50%;
                    color: #ccc;
                    margin-top: -8px;
                }
            }
            &.active,
            &:hover{
                .inner-link{
                    box-shadow: 0 0  8px 2px #ebebeb;
                }
            }
        }
        .inner-link{
            color: @text-color;
            display: block;
            padding: @padding-mid-v @padding-base-h*2;
        }
    }
}
//新建客户
.create-customer-wrap{
    .customer-img-wrap{
        border: 1px solid @border-color;
    }
    .customer-img{
        width: 118px;
        height: 147px;
    }
}
//余额账本查询
.floader-table{
    .title{
        th{
            background: @brand-primary;
            color: #fff;
            font-size: @font-size-mid;
            border: none;
        }
    }
    .f-header{
        th{
            background: #ECF4FE;
            border: none;
            border-top: @padding-small-v solid #fff;
        }
    }
    >tbody>tr>td{
        border: none;
        padding: 0;
        // padding-right: 0;
        white-space: nowrap;
    }
}
/*楠神的*/
.tooltip-inner {
    max-width: 200px;
    padding: 3px 8px;
    color: #000;
    text-align: center;
    background-color: #fffddd;
    border-color: #ffae3f;
    border-radius: 0px;
    border-style:solid;
    border-width:1px;
  }
  .tooltip.left .tooltip-arrow {
    top: 50%;
    right: 0;
    margin-top: -5px;
    border-width: 5px 0 5px 5px;
    border-left-color: #ffae3f;
  }
  .tooltip.bottom .tooltip-arrow {
    top: 0;
    left: 50%;
    margin-left: -5px;
    border-width: 0 5px 5px;
    border-bottom-color: #ffae3f;
  }
  .tooltip.bottom-right .tooltip-arrow {
    top: 0;
    right: 5px;
    border-width: 0 5px 5px;
    border-bottom-color: #ffae3f;
  }
  .tooltip.bottom-left .tooltip-arrow {
    top: 0;
    left: 5px;
    border-width: 0 5px 5px;
    border-bottom-color: #ffae3f;
  }
  
  .text-primary-table td{
    color:#00a2ff;
  }
  
  .table.table-colorfull > tbody > tr.selected > td{
    background-color: #fde5c6;
  }